<template>
    <div class="tutorial-manager">
        <div class="button-part">
            <el-button
            type="success"
            @click="handleAdd()">添加公告</el-button>
        </div>
        <!-- 公告表格 -->
         <el-table
            v-loading="listLoading"
            :data="list"
            element-loading-text="Loading"
            border
            fit
            highlight-current-row
            >
            <el-table-column align="center" label="公告ID" width="150" >
                <template slot-scope="scope">
                  {{ scope.row.bulletinId}}
                </template>
            </el-table-column>
            <el-table-column label="公告内容"  align="center">
                <template slot-scope="scope">
                <div class="content">{{ scope.row.content}}</div>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="260"  align="center">
                  <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>

            </el-table>
         <!-- 公告表格结束 -->

         <!-- 公告编辑框 -->
         <el-dialog :title="showText" :visible.sync="dialogFormVisible">
            <el-form :model="editInfo">
                <!-- <el-form-item label="公告标题" :label-width="formLabelWidth">
                <el-input v-model="editInfo.titleContent" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="公告标号" :label-width="formLabelWidth">
                <el-input v-model="editInfo.titleNum" autocomplete="off"></el-input> -->
                <!-- </el-form-item> -->
                <el-form-item label="公告内容" :label-width="formLabelWidth">
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 14}"
                    placeholder="请输入内容"
                    v-model="editInfo.content">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addBulletin()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {getAllBulletin,
addBulletin,
updateBulletin,
deleteBulletin} from '../../network/bulletin.js'
export default {
    data(){
        return{
            list: [
                {
                    bulletinId:1,
                    content:'内容内容内容...............'
                },
                {
                    bulletinId:2,
                    content:'内容内容内容...............'
                }
            ],
            listLoading: false,
            dialogFormVisible:false,
            showText:'添加公告',
            editInfo:{
               bulletinId:null,
               content:''
            },
            formLabelWidth: '120px'
        }
        
    },
    created(){
        this.getAllBulletin();
    },
    methods:{
        handleDetail(index,row)
        {
            const tutoId=row.tutoId;
            console.log(tutoId);
            this.$emit('routeChange0',{to:`/index/tutorialDetail/${tutoId}`,name:'训练公告详情'})
            this.$router.push(`/index/tutorialDetail/${tutoId}`)
        },
        handleEdit(index, row)
        {
            const tutoId=row.tutoId;
            console.log(tutoId);
            this.dialogFormVisible=true;
            this.$turn(this.editInfo,this.list[index])
            this.showText='修改训练公告'
        },
        handleAdd()
        {
           this.showText='添加训练公告';
           this.clearInfo();
           this.dialogFormVisible=true;
        },
        clearInfo()
        {
           this.editInfo.titleContent='';
           this.editInfo.titleNum='';
           this.editInfo.content='';
        },
        //添加公告
        async addBulletin()
        {
           if(this.showText=='修改训练公告')
           {
               this.updateBulletin();
               return;
           }
           let res=await addBulletin(this.editInfo.content);
           this.dialogFormVisible=false;
           console.log(res);
           this.getAllBulletin();
           this.$message({
               message:'添加公告成功!',
               type:'success'
           })
        },
        async updateBulletin()
        { 
            let res=await updateBulletin(this.editInfo);
            console.log(res);
            this.getAllBulletin();
            this.$message({
                message:'修改公告成功!',
                type:'success'
            })
            this.dialogFormVisible=false;
        },
        async handleDelete(index,row) {
            console.log(row.bulletinId);
            let confirm=await this.$confirm(`此操作将永久删除公告"${row.bulletinId}", 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            })
            if(confirm=='confirm')
            {
                let data=await deleteBulletin(row.bulletinId);
                console.log(data);
                this.getAllBulletin();
                this.$message({
                    type:'success',
                    message:'删除公告成功!'
                })
            }
      },
      async getAllBulletin()
      {
           let res=await getAllBulletin();
           console.log(res);
           this.list=res.data.data.bulletinList;
      }
    }
}
</script>

<style scoped>
     .tutorial-manager
     {
         width: 100%;
         position: relative;
     }
     .button-part
     {
         position: absolute;
         top:-60px;
         right:10px;
         z-index:100;
     }
     .content
     {
        width:700px;    
        overflow: hidden;    
        text-overflow:ellipsis;    
        white-space: nowrap;
     }
    
</style>