import request from '../network/request.js'
//获取所有公告内容
export function getAllBulletin() {
    return request({
        method: 'get',
        url: '/bulletin/getAll'
    })
}

//添加公告内容
export function addBulletin(content) {
    return request({
        method: 'post',
        url: '/bulletin',
        data: {
            content
        }
    })
}

//更新公告
export function updateBulletin(data) {
    const { bulletinId, content } = data;
    return request({
        method: 'put',
        url: `/bulletin/modify/${bulletinId}`,
        data: {
            content
        }
    })
}

//删除公告
export function deleteBulletin(bulletinId) {
    return request({
        method: 'delete',
        url: `/bulletin/${bulletinId}`
    })
}